
.hero-box {
    position: absolute;
    top: 150px;
    left: 15%;
    right: 15%;
    text-align: center;
    transform: translate(-5%, -15%);
}
.heading-primary {
    color: #494744;
    text-transform: uppercase;
}

.heading-primary-main {
    color: #fdf8ed;
    display: block;
    font-size: 40px;
    font-weight: 700;
    letter-spacing: 25px;
    /*animation-name: moveInLeft;
    animation-duration:  1s;
    animation-timing-function: ease-in;*/
}

.heading-primary-sub {
    display: block;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 10px;
    /*animation-name: moveInRight;
    animation-duration: 1s;
    animation-timing-function: ease-in;*/
}

@keyframes moveInLeft {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }
    80%{
        opacity: 0.8;
        transform: translateX(20px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes moveInRight {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }
    80%{
        opacity: 0.8;
        transform: translateX(-20px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}